.notice {
  font-family: var(--font-primary);
  padding: 70px 0 143px;
}

.notice__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
}

.notice__swiper {
  width: 100%;
  height: 286px;
  overflow: hidden;
}

.notice__swiper-slide {
  padding: 0 10px;
}

.notice__card {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 24px;
  overflow: hidden;

  img {
    display: blocl;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &::before {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    width: 350px;
    height: 350px;
    background: #fa9397;
    border-radius: 50%;
    translate: -50% 0;
    transition: var(--transition-tf) var(--transition-d)
  }
}

@media(hover: hover) {
  .notice__card {
    &:hover {
      &::before {
        top: 30px;
      }
    }
  }
}

.notice__card-text {
  position: absolute;
  bottom: 16px;
  left: 16px;
  width: 88%;
  margin: 0;
  font-size: 20px;
  line-height: 28px;
  font-weight: 700;
  color: rgb(var(--color-light));
}

@media(max-width: $mobile) {
  .notice {
    padding: 50px 0 80px;
  }

  .notice__container {
    align-items: flex-start;
  }
}
